body {
  margin: 0;
  padding: 0;
  background-color: #e0e0e0;
  display: flex;
}

.LogoContainer {
  display: flex;
  justify-content: right;
}

.Logo{
  display: flex;
  height: 200px;
  width: 220px;
  margin-top: 60px;
  margin-right: 200px;
}

.instructions{
  display: flex;
  flex-direction: column;
  margin-left: 15px;
  justify-content: flex-start;
}

Canvas {
  height: 500px;
  width: 700px;
  background-color: white;
  border-color: black;
  border-style: solid;
  border-width: 1px;
}

.canvasAlignment{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -75px;
}

.controlPanel{
  display: flex;
  justify-content: center;
}

.submitButton{
  background-color: pink;
  height: 30px;
  width: auto;
  margin: 5px;
}

.clearButton{
  background-color: pink;
  height: 30px;
  width: auto;
  margin: 5px;
}

.colorPicker{
  height: 30px;
  width: 30px;
  border-radius: 50%;
  border: 3px solid rgb(214, 214, 214);
  transition: transform .5s;
  margin-top: 5px;
}

.colorPicker:nth-child(5){
  background-color: #000;
}
.colorPicker:nth-child(6){
  background-color: red;
}
.colorPicker:nth-child(3){
  background-color: green;
}
.colorPicker:nth-child(4){
  background-color: yellow;
}







h1{
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

}

h3{
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
